main{
  text-align: center;
  font-family: sans-serif;
}

img{
  width: 500px;
  height: 500px;
  object-fit: cover;
}

nav ul li {
  display: inline;
  margin: 0 10px;
}

button{
  padding: 5px 15px;
  border-radius: 5px;
  background: rgb(107, 107, 107);
  color: #fff;
}

button:hover{
  /* padding: 6px 16px; */
  transform: scale(1.1);
}

* {
  transition: all .4s ease;
}

ul{
  list-style: none;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
